.wrapper {
  position: relative;
  width: max-content;
  height: max-content;
}
.main {
  position: relative;
  text-align: center;
  cursor: pointer;
  font-size: 20px;
}
.childBlock {
  position: absolute;
  left: 50%;
  top: 100%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background: white;
}
.childItem {
  font-size: 70%;
  white-space: nowrap;
  text-align: start;
  position: relative;
  cursor: pointer;
}