.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
}
.navBlock {
  position: relative;
  display: flex;
  grid-gap: 30px;
  margin: 40px 0 20px;
}
.navBlock .active {
  color: red;
}
.navBlock a {
  font-size: 130%;
  font-weight: 500;
}
.logo {
  position: relative;
  width: 100px;
  height: 100px;
  display: flex;
}
.logo img {
  position: relative;
  width: 100%;
  height: 100%;
}
.long {
  position: relative;
  width: 100%;
  height: 1px;
  background: #c4a370;
  border-radius: 50%;
  opacity: 0.5;
}