.item {
  position: relative;
  width: 100%;
  height: max-content;
  display: flex;
  flex-direction: column;
  text-align: center;
  cursor: pointer;
  padding-bottom: 25px;
}
.blockPhoto {
  position: relative;
  overflow: hidden;
}
.backPhoto {
  position: relative;
  width: 100%;
}
.actionPhoto {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(1);
  width: 100%;
  transition: 0.3s;
}
.name {
  margin-top: 15px;
  font-size: 100%;
  font-weight: 400;
  text-transform: uppercase;
}
.price {
  margin-top: 5px;
  font-size: 100%;
  font-weight: 600;
}
.item:hover .actionPhoto {
  transform: translate(-50%, -50%) scale(1.1);
}
.item p {
  transition: opacity 0.3s;
}
.item:hover p {
  opacity: 0.7;
}

.newBlock {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 35px;
  height: 35px;
  background: #c4a370;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 70%;
  font-weight: 900;
  color: #ffffff;
  transition: 0.3s;
  box-shadow: -2px 3px 7px 0px rgba(0, 0, 0, 0.1);
}
.item:hover .newBlock {
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.1);
}
.stock {
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
  color: rgba(128, 128, 128, 0.4);
  font-size: 80%;
  margin-top: 10px;
}