.wrapper {
  position: relative;
  padding: 50px;
  padding-bottom: 30px;
}
.block {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.name {
  text-align: center;
  font-weight: 400;
  margin-top: 100px;
}
.navBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
.navBlock a {
  font-weight: 400;
}
.requisitesBlock {
  position: relative;
  text-align: end;
}
.long {
  position: relative;
  width: 100%;
  height: 1px;
  background: #c4a370;
  border-radius: 50%;
  margin: 20px 0;
  opacity: 0.5;
}
.BankIconBlock {
  position: relative;
  display: flex;
  opacity: 0.4;
  justify-content: center;
  grid-gap: 10px;
  margin: 40px 0;
}
.BankIconBlock img{
  height: 20px;
}