.wrapper {
  position: relative;
  padding: 0;
  margin: 20px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 100;
}
.block {
  position: relative;
  display: flex;
  grid-gap: 40px;
}
.block a {
  font-weight: 500;
  font-size: 100%;
}
